import { ColumnDef, Row } from '@tanstack/react-table';
import adel from '@/assets/imgs/user.png';
import ViewIcon from '@/components/icons/ViewIcon';
import { Link } from 'react-router-dom';
import { SalaryList } from '../../types/salary.types';

// Create a separate component for the actions cell
const ActionsCell = ({ row }: { row: Row<SalaryList> }) => {
    return (
        <div className="cursor-pointer flex justify-center">
            <Link to="/human-resources/salaries-details/add-allowance">
                <ViewIcon className="text-primery-blue-400" />
            </Link>
        </div>
    );
};

export const columns: ColumnDef<SalaryList>[] = [
    {
        accessorKey: 'id',
        header: () => {
            return (
                <div className="text-sm text-nowrap px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    اسم الموظف
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                {row.original.name}
            </div>
        ),
    },
    {
        accessorKey: 'name',
        header: () => {
            return (
                <div className="text-sm text-nowrap px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    رقم الموظف
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">{row.original.id}</div>
        ),
    },
    {
        accessorKey: 'status',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    القسم
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                    {row.original.department}
                </div>
            );
        },
    },
    {
        accessorKey: 'date',
        header: () => {
            return (
                <div className="text-sm text-nowrap px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    الراتب الاساسي
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                    {row.original.basic_salary}
                </div>
            );
        },
    },
    {
        accessorKey: 'location',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    البدلات
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                    {row.original.total_allowances}
                </div>
            );
        },
    },
    {
        accessorKey: 'location',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    الخصومات
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                    {row.original.total_deductions}
                </div>
            );
        },
    },
    {
        accessorKey: 'location',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    الحالة
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-xs font-medium tracking-normal leading-[115%]">
                    <p className="border text-primery-blue-50 bg-[#22AD5C] border-[#22AD5C] p-1 w-fit rounded-sm">
                        {/* {row.original.status} */}
                        مدفوع
                    </p>
                </div>
            );
        },
    },
    {
        accessorKey: 'location',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    الاجمالي
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    {row.original.total_salary}
                </div>
            );
        },
    },
    {
        id: 'actions',
        header: () => {
            return (
                <div className="text-sm text-nowrap text-center px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    التقرير
                </div>
            );
        },
        cell: ({ row }) => <ActionsCell row={row} />,
    },
];
